.menu ul:after {
	clear: both;
	content: "";
	display: block;
}
.menu > ul > li {
	float: left;
	width: 25%;
	border-right: 1px solid #3366cc;

}
.menu > ul > li:first-child {
	border-left: 1px solid #3366cc;
}
.menu > ul li > a {
	display: block;
	height: 50px;
	text-align: center;
	line-height: 50px;
	text-decoration: none;
	font-size: 21px;
	color:#3366cc;
}
.menu > ul li > a:hover {
	background-color: #3366cc;
	color:white;
	cursor: pointer;
}
.products-price,.products-count {
	color:white;
	font-size: 17px;
	margin:5px 0;
}
.header {
	background-color: rgba(255,255,255,0.75);
	padding: 25px 0;
}
.products-list-item {
    color:white;
    border:1px solid white;
    padding:15px;
    margin: 10px;
    background: rgba(51,102,204,0.75);
    font-size: 21px;
    letter-spacing: 0.5px;
    
}
.product-name {
    font-size: 26px;
    margin-bottom: 15px;
    font-weight: bold;
}
.product-description {
    font-weight: 300;
    margin-bottom: 15px;
}
.product-features {
    font-weight: bold;
    text-transform: uppercase;
}
.product-type,.product-screen-size,.product-capacity {
    margin-bottom: 15px;
}
.btn-add-to-cart {
    text-transform: uppercase;
    font-size: 21px;
    background: #3366cc;
    width: 215px;
    height: 50px;
    line-height: 50px;
    margin: 0 auto;
    display: block;
    color:white;
    border: 1px solid white;
}
.btn-add-to-cart:hover {
    cursor: pointer;
    background: #6699cc;
}
.product-price {
    font-size: 36px;
    text-align: center;
    margin: 10px 0;
}
.product-image {
    background-color: white;
    text-align: center;
    padding: 15px 0;
}
.products-list  h2 {
    font-size: 32px;
    color: white;
    margin-top: 15px;
    text-align: center;
}
.cart-product-list-item-description {
    margin: 25px 0;
    padding: 15px;
    border: 1px dotted;
    border-radius: 10px;
    color: black;
}
.cart-product-list-item-description img {
    display: block;
    margin: 0 auto;
}
.cart-extended-name {
    font-size: 20px;
    text-align: center;
    margin-bottom: 15px;
}
.cart-extended-price {
 font-size: 13px;
 font-style: italic;
 margin-bottom: 10px;
}
.bold {
    font-weight: bold;
}
.cart-extended-sum {
    font-size: 16px;
    text-align: right;
}
.sum-price {
    font-size: 20px;
    color: #3366cc;
}
.cart-product-list-item-description {
    background: white;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
	box-sizing: border-box;
}
body {
	background-image: url(data:image/png;base64,);
}
.container {
	
	margin:0 auto;
}
.row:after {
	display: block;
	clear: both;
	content:"";
}
[class*="col-"] {
	float: left;
}
.col-lg-3 {
	width: 25%;
}
.col-lg-6 {
	width: 50%;
}
.col-lg-9 {
	width: 75%;
}
.text-center {
	text-align: center;
}

@media(min-width: 768px) {
	.container {
		width: 750px;
	}
}
@media(min-width: 992px) {
	.container {
		width: 970px;
	}
}
@media(min-width: 1200px) {
	.container {
		width: 1170px;
	}
}
